.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 50px 10%;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.nav-pills {
    position: fixed;
    top: 0;
    left: 0;
    background: #000;
    min-height: 65px;
    width: 100%;
    text-align: center;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
    font-size: 2em;
    font-weight: bold;
    z-index: 99;
    white-space: nowrap;
}

.nav-pills a {
    color: #fff;
}

.nav-pills a:hover {
    color: #fff;
    background-color: #5133a4 !important;
}

.desc-normal {
    color: #fff;
}

.desc-correct {
    color: #0f0;
}

.desc-incorrect {
    color: #f00;
}
.titlebox {
    background-size: 100%;
    background-repeat: no-repeat;
    width: 100%;
    min-height: 280px;
    text-align: center;
    vertical-align: center;
    font-family: 'Seymour One', sans-serif;
    margin-bottom: 20px;
}

.titlebox h1 {
    margin-top: 90px;
    font-size: 3.5em;
}

.titlebox p {
    font-size: 1.8em;
}
.card {
  background: #fff;
  border-radius: 2px;
  height: 160px;
  overflow: hidden;
  margin: 1rem;
  position: relative;
  width: 250px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  width: 100%;
  height: auto;
  overflow: hidden;
  text-align: center;
}
.card > .img-container img {
  margin-top: 10px;
  width: 100%;
  height: 140px;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
